import React from "react";
import "./NavBar.css";
import { Link } from "react-router-dom";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import $ from "jquery";
import logo from "../img/itpeaksLogo5.1.png";
$(window).on("scroll", function() {
  if($(window).scrollTop()) {
        $('.navItems').addClass('royalBlue');
  }

  else {
        $('.navItems').removeClass('royalBlue');
  }
})
function NavBar() {
  return (
    <div>
    <Row className="navBar-main-top-row">
      <Col md={12} className="navbar-main-col">
      <div className="navBar">
        <Nav className="navItems">
          <div>
            <Link to="/">
              <Navbar.Brand className="navBarBrand" href="/">
                <img
                  src={logo}
                  className="navBarBrand-img"
                  width="150"
                  height="90"
                  alt=""
                  href="/"
                />
              </Navbar.Brand>
            </Link>
          </div>
          <Navbar collapseOnSelect>
            <Navbar.Brand className="navBarBrand2" href="/">
              <Link to="/">
                <img
                  src="https://i.ibb.co/RBwNtkX/itpeaks-Logo-Edited.png"
                  width="70"
                  height="70"
                  className="navBarBrand-img"
                  alt=""
                  href="/"
                />
              </Link>
            </Navbar.Brand>
          </Navbar>
          <Navbar
            collapseOnSelect
            expand="xl"
            className="navbar-bootstrapNavbar"
          >
            <Navbar.Toggle aria-controls="responsive-navbar-nav" />
            <Navbar.Collapse id="responsive-navbar-nav">
              <Nav className="mr-auto nav-container">
                <div className="navbar-items-div">
                  <Nav.Link className="contactUs-Nav nav-link">
                    <Link to="/about" className="aboutLink">
                      About
                    </Link>
                  </Nav.Link>
                  <Nav.Link className="blog-Nav nav-link" to="/services">
                    <Link to="/services" className="servicesLink">
                      Services
                    </Link>
                  </Nav.Link>

                  <Nav.Link className="blog-Nav nav-link">
                    <Link to="/blog" className="contactLink">
                      Blog
                    </Link>
                  </Nav.Link>
                  <Nav.Link className="blog-Nav nav-link">
                    <Link to="/joinOurTeam" className="contactLink">
                      Job Board
                    </Link>
                  </Nav.Link>
                  <Nav.Link className="contactUs-Nav nav-link">
                    <Link to="/contact" className="contactLink">
                      Contact
                    </Link>
                  </Nav.Link>
                </div>
              </Nav>
            </Navbar.Collapse>
          </Navbar>
        </Nav>
      </div>
      </Col>
      </Row>
    </div>
  );
}

export default NavBar;
